*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.btn{
  
  border: solid 1px black;
  padding: 12px 32px;
  text-transform: uppercase;
  background-color: rgb(230, 30, 140);
  color: white;
  font-weight: 600;
  cursor: pointer;
}
.btn-light{
  border: solid 1px black;
  padding: 12px 32px;
  text-transform: uppercase;
  background-color: rgb(87, 148, 129);
  color: rgb(255, 255, 255);
  font-weight: 600;
  cursor: pointer;
 
 background: transparent;
}
.btn:hover{
  background-color: aqua;

}