.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}
h1{
    color: black;
}
.nav-menu{
    display: flex;
}
.nav-menu li{
    padding: 0 1rem;
    list-style: none;
}
.nav-menu li a{
    font-size: 1.2rem;
    color: black;
    font-weight: 500;
}
.hamburger{
    display: none;
}
@media screen and (max-width: 1040px){
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(255,255,255, 0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.3s;
        
    }
    .nav-menu.active{
        left:0;
    }
    .nav-menu li{
        padding: 1rem 0;
    }
    .nav-menu li a{
        color: black;
        font-size: 2rem;

    }
    .hamburger{
        display: initial;
    }
    
}