.project-heading{
    text-align: center;
    padding: 4rem 0 2rem 0;
}
.project-container{
    width: 800px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3 ,1fr);
    grid-gap: 40px;
}
.project-card{
    background: rgb(10, 10, 45);
    padding: 1.2rem 1 rem;
}
.project-card img{
    width:100%;
}
.project-title{
   
    justify-content: center;
    align-items: center;
    color: aliceblue;
    margin-left: 50px;
    
    padding: 1rem 0;

}
.pro-details p{
    color: aliceblue;
    justify-content: center;
    align-items: center;
    margin-left: 30px;
    padding-bottom: 1rem;
    font-size: 1.1rem;
    text-align: justify;
}
.pro-btn{
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 0;
}
.pro-btn .btn{
    padding: 0.5rem 1rem;
}
@media screen and(max-width:740px) {

    .project-container{
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }
}