label{
    margin-bottom: 0.5rem;
    color: white;
}
div.form{
    width: 100%;
    background-color:rgb(52, 52, 67);
}
form{
  
    height: 100%;
    background-color: rgb(52, 52, 67);
    display: flex;
    flex-direction: column;
    padding: 4rem 1rem;
    margin: auto;
    max-width: 600px;

}
form input,form textarea{
    margin-bottom: 1rem;
    padding: 10px 18px;
    font-size: 1.2rem;
    background-color:aliceblue;
    border-color: grey;
    color: black;
}