
.hero2{
    width: 100%;
    height: 100vh;
    opacity: 0.6;
    position: relative;
}
.hero2::before{
    content: "";
    background-image: url("https://images.unsplash.com/photo-1587620962725-abab7fe55159?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1031&q=80");
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;

}
.heading{
   
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.hero2 h1{
    color: aliceblue;
    font-size: 2.4rem;
}
.hero2 p{
    color: aliceblue;
    font-size: 1.4rem;
    text-align: center;
}
@media screen and(max-width:640px) {
    .hero2 h1{
        font-size: 2rem;
    }
}